body {
  background: #FFF;
  line-height: 1.4;
}

.block {
  display: flex;
}

.delivery-info {
  padding: 64px 0 32px;
  color: #483d3c;
}

.delivery-info__title {
  color: #6aa943;
}

.delivery-info__wrap {
  margin: 25px -46px 0;
  display: flex;
  justify-content: center;
}

.delivery-info__item {
  padding: 0 46px;
}

.delivery-info__btn {
  text-align: center;
  margin-top: 30px;
  min-width: 170px;
}

.delivery-item {
  max-width: 220px;
  text-align: center;
}

.delivery-item__img-wrap {
  min-height: 200px;
}

.delivery-item__text {
  margin-top: 30px;
  font-size: 27px;
  font-weight: 700;
}

.delivery-item__text span {
  text-transform: uppercase;
  display: block;
}

.delivery-item_big {
  max-width: 350px;
}

.btn {
  padding: 12px 28px;
  font-family: 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  line-height: 1;
  text-transform: uppercase;
  vertical-align: top;
  display: inline-block;
  border-radius: 20.5px;
  background-color: #d0661b;
  transition: background-color .3s, color .3s;
}

.title {
  font-family: 'Circe', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-size: 40px;
  font-weight: 400;
  line-height: 1;
  font-family: 'Circe', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
}
